<template>
  <div>
    <Title title="">
      <div><el-button type="primary" @click="onAdd" size='small' round></el-button></div>
    </Title>
    <div class="menbers">
      <div v-for="member in memberList" :key="member.id">
        <div>{{`ID${member.userName} / NickName${member.nickName}`}}</div>
        <div><i class="el-icon-delete" title="" @click="del(member.id)"></i></div>
      </div>
    </div>
    <AddMemberDialog v-if="dialogShow" @onClose="onDialogClose" @onSuccess="onSuccess"/>
  </div>
</template>

<script>
import Title from '../../../components/TabContentTitle'
import { getMembers, deleteMember } from '../../../service/team'
import AddMemberDialog from './AddMemberDialog.vue'
export default {
  data () {
    return {
      memberList: [],
      dialogShow: false
    }
  },
  components: {
    Title,
    AddMemberDialog
  },
  async created () {
    this.getData()
  },
  methods: {
    async getData () {
      this.memberList = await getMembers(this.$route.query.teamId)
    },
    onAdd () {
      this.dialogShow = true
    },
    onSuccess () {
      this.dialogShow = false
      this.getData()
    },
    del (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await deleteMember(id)
        if (res) {
          this.$message({ type: 'success', message: '!' })
          this.getData()
        }
      }).catch((err) => {
        console.error(err)
        this.$message({ type: 'info', message: '' })
      })
    },
    onDialogClose () {
      this.dialogShow = false
    }
  }
}
</script>

