.menbers>div{
  padding: 15px 10px;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content:space-between;
  font-size: 14px;
  i{
    color:#ea6f5a;
    cursor: pointer;
  }
}
