<template>
  <div>
        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="50%"
            :before-close="onCancel">

            <el-form ref="form" :model="form" label-width="80px" size='small' :rules="rules">
                <el-form-item label="" prop="weeklyTitle">
                    <el-input v-model="form.weeklyTitle" maxlength="100" show-word-limit placeholder="XXXX - XXX"></el-input>
                </el-form-item>
                <el-form-item label="" prop="coverUrl">
                    <el-input v-model="form.coverUrl" maxlength="500" show-word-limit placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit"></el-button>
                    <el-button @click="onCancel"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
  </div>
</template>

<script>
import { createWeekly } from '../../../service/weekly'
const ON_CLOSE = 'onClose'
export default {
  data () {
    return {
      dialogVisible: true,
      form: {
        weeklyTitle: '',
        coverUrl: ''
      },
      rules: {
        weeklyTitle: [{ required: true, message: '', trigger: 'blur' }],
        coverUrl: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    onSubmit () {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          const res = await createWeekly(Object.assign({ teamId: this.$route.query.teamId }, this.form))
          if (res) {
            this.$message.success('')
            this.$emit(ON_CLOSE)
          }
        }
      })
    },
    onCancel () {
      this.$emit(ON_CLOSE)
    }
  }
}
</script>

<style>
.el-dialog__body{
    padding-top: 15px;
    padding-bottom: 10px;
}
</style>
