<template>
  <div>
      <el-form ref="signInForm" :model="form" class="form" :rules="rules">
        <el-form-item prop='nickName'>
            <el-input v-model="form.nickName" maxlength="10" show-word-limit prefix-icon="el-icon-s-custom" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop='userName'>
            <el-input v-model="form.userName" maxlength="12" show-word-limit prefix-icon="el-icon-user" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop='password'>
            <el-input v-model="form.password" type="password" maxlength="12" show-word-limit prefix-icon="el-icon-unlock" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop='email'>
            <el-input v-model="form.email" maxlength="50" show-word-limit prefix-icon="el-icon-message" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="success" @click="onSubmit" round class="btn"></el-button>
        </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { checkUserName, checkNickName, checkEmail, register } from '../../../service/login'
const ON_SUCCESS = 'onSuccess'

// 
const userName = async (rule, value, callback) => {
  if (!value) { return callback(new Error('')) }
  const res = await checkUserName(value)
  res ? callback(new Error('')) : callback()
}
// 
const nickName = async (rule, value, callback) => {
  if (!value) { return callback(new Error('')) }
  const res = await checkNickName(value)
  res ? callback(new Error('')) : callback()
}
// 
const email = async (rule, value, callback) => {
  if (!value) { return callback(new Error('')) }
  const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
  if (mailReg.test(value)) {
    // 
    const res = await checkEmail(value)
    res ? callback(new Error('')) : callback()
  } else {
    return callback(new Error(''))
  }
}

export default {
  data () {
    return {
      dialogVisible: true,
      form: {
        nickName: '',
        userName: '',
        password: '',
        email: ''
      },
      rules: {
        userName: [{ validator: userName, trigger: 'keyup' }],
        nickName: [{ validator: nickName, trigger: 'keyup' }],
        email: [{ validator: email, trigger: 'keyup' }],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 12, message: ' 6  12 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit () {
      this.$refs.signInForm.validate(async (valid) => {
        if (valid) {
          const res = await register(this.form)
          this.$refs.signInForm.resetFields()
          this.$message.success(`DDD${res.registerRank}.`)
          this.$emit(ON_SUCCESS)
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
.form{
    margin: 20px 30px 0 30px;
    text-align: center;
}
.btn{
    width: 200px;
}
</style>
