<template>
  <div>
        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="50%"
            :before-close="onCancel">

            <el-form ref="form" :model="form" label-width="80px" size='small'>
                <el-form-item label="">
                    <el-input v-model="form.userName" maxlength="12" show-word-limit style="width:300px" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit"></el-button>
                    <el-button @click="onCancel"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
  </div>
</template>

<script>
import { addTeamMember } from '../../../service/team'
const ON_CLOSE = 'onClose'
const ON_SUCCESS = 'onSuccess'
export default {
  data () {
    return {
      dialogVisible: true,
      form: {
        userName: ''
      }
    }
  },
  methods: {
    async onSubmit () {
      const res = await addTeamMember({ userName: this.form.userName, teamId: this.$route.query.teamId })
      if (res) {
        this.$message({
          type: 'success',
          message: '',
          onClose: () => {
            // window.location.reload()
            this.$emit(ON_SUCCESS)
          }
        })
      }
    },
    onCancel () {
      this.$emit(ON_CLOSE)
    }
  }
}
</script>

<style>
.el-dialog__body{
    padding-top: 15px;
    padding-bottom: 10px;
}
</style>
